.preview-box{
    display: inline-flex;
    width: auto;
    height: 340px;
    margin:10px 20% 100px 20%;
    overflow: hidden;
    padding:0px 10px 0px 0px;
    border-radius: 4px;
    position: relative;
}
.ProductImagePreview{
    width: 340px;
    height: auto;
    border-radius: 4px;

}
.productname{
    margin:10px 10px 20px 10px;
    text-align: start;
    font-size: 24px;
    border-bottom: 1px solid #ed9220;
        border-top: 1px solid #ed9220;
        padding: 10px 0px 10px 0px;
}
.discriptiontext{
    margin:10px 10px 10% 10px;
    text-align: start;
    width: auto;
    
}
.Price{
    margin: 20px 10px 10px 10px;
    position: absolute;
    bottom: 0;
    font-size: 24px;
}
.discription{
    margin:10px;
    text-align: start;
    
}
.AddtoCartBtn{
    background-color: #ed9220;
    color:#f2f2f2;
    font-size: 18px;
    outline: none;
    border-color: transparent;
    cursor: pointer;  
    padding: 5px;
    width: fit-content;
    border-radius: 4px;
    margin: 20px 10px 10px 10px;
    position: absolute;
    bottom: 0;
    right: 0;
}

@media only screen and (max-width: 1000px) {
    .preview-box {
            display: flex;
            flex-direction: column;
            height: auto;
            margin:10px 5% 20px 5%;
            padding-bottom: 20px;


    }
    .ProductImagePreview{
        width: 360px;
        height: auto;
    }
  }