@import url('https://fonts.googleapis.com/css2?family=Lobster+Two:wght@700&display=swap');
.searchNavigation{
    background: #232f3e;
    width: 100%;
    padding: 20px 0px 20px 0px;
    color: #f2f2f2;
    user-select: none;
    
}
.logoResize{
text-align: center;
}
.Logo{
    width: 100px;
    display: inline-block;
    font-family: 'Lobster Two', cursive;
    font-size: 3em;
    color: #FF9900;
    cursor: pointer;   
}
.SearchInput{
    font-size: 16px;
    border-radius: 4px;
    padding: 5px 10% 5px 15px;
    margin-right: -1.1em;
    outline: none;
    color:#111;
    background-color: #f2f2f2;
    border-color: transparent;
}
.SearchBtn{
    font-size: 18px;
   border: none;
    border-radius: 0px 4px 4px 0px;
    background:#ed9220;
    padding: 10px 15px 10px 15px;
    margin-left: -1.1em;
    margin-right: 3%;
    color:#f2f2f2;    
    outline-style: none;
    cursor: pointer;
    position: relative;
}
.SearchBar{
    margin-top: 10px;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
}

.shoppingCart
{
    text-align: center;
    color: #f2f2f2;
    font-size: 1.1em;
    margin:0 ;
    padding: 0px;
    position: absolute;
    top: 0px;
    right:0px;
    margin: 30px 10px 0px 0px;
    cursor: pointer;
}


.Login{
    text-align: center;
    color: #f2f2f2;
    font-size: 1.1em;
    margin:0 ;
    padding: 0px;
    position: absolute;
    top: 0px;
    left:0px;
    margin: 30px 0px 0px 10px;
    cursor: pointer;
}

