.Box{
    text-align: center;
   
}
.LoginForm{
    margin-top: 10px;
    display: inline-flex;
    flex-direction: column;
    border-radius: 4px;
    width: max-content;
    color: #000000;
    padding:0px 25px 30px 25px;

}
.LoginLabel{
    font-size: 24px;

}
.EmailLabel{
    font-size: 16px;
    display: inline;
    text-align: right;

}
.EmailInput{
    border-style: none;
    width: 200px;
    font-size: 14px;
    margin-left: 10px;
    padding: 10px;
    border-radius: 4px;
}

.PasswordLabel{
    font-size: 16px;
    display: inline;
    text-align: right;
}
.PasswordInput{
    border-style: none;
    width: 200px;
    font-size: 14px;
    margin-left: 10px;
    padding: 10px;
    border-radius: 4px;
}
.LoginBtn{
    border-style: none;
    width: fit-content;
    align-self: flex-end;
    margin-top: 20px;
    padding: 10px 20px 10px 20px;
    border-radius: 4px;
    color:#f2f2f2;
    cursor: pointer;
    font-size: 16px;
    background-color: #ed9220;
    ;
}

.GoRegisterLabel{
    padding-top: 10px;
    color: #000000;
}
.GoRegisterBtn{
    opacity: 1;
    color:#146eb4;
    cursor: pointer;

}