.PreviewBtn{
    background-color: #ed9220;
    color:#f2f2f2;
    font-size: 18px;
    outline: none;
    border-color: transparent;
    cursor: pointer;  
    padding: 5px;
    width: 100%;
   
}

.Card{
    background: #f2f2f2;
    width: 330px;
    height: auto;
    margin: 10px 20px 20px 20px;    
}

.ProductName{
position: relative;
width: auto;
color: #010a1c;
text-align: left;
margin-left: 10px;
margin-top: 0px;
}
.ProductImage{
width: 100%;
height: 330;
}

.container1
{
display:inline-flex;
flex-wrap: wrap;
justify-content: center;
}
.grid{
	display: grid;
	grid-gap: 20px;
    grid-template-columns: repeat(auto-fill,minmax(350px,2fr));
    
}


