.FilterNavigation{
    background: #f2f2f2;
    width: 100%;
    height: 45px;
    padding-top: 5px;
    top: 0;
    user-select: none;  
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    display: inline-flex;
    justify-content: right;
}


.Container{
    background-color: #f1f1ef;
    display: flex;
	justify-content: flex-end;
    flex-wrap: wrap;
}
.Sort{
  margin:10px 0px 0px 0px;
  color: #000000;
  margin-left: 20px;
}
.Filter{
    background: #232f3e;
    border-style: none;
    font-size: 16px;
    width: 190px;
    padding: 5px 0px 5px 5px;
    float: right;
    margin-right:3%;
    color: white;
    height: 40px;
    margin-left: 5px;
    
}

select {

    /* styling */
    background-color: white;
    border: thin solid blue;
    border-radius: 4px;
    display: inline-block;
    font: inherit;
    line-height: 1.5em; 
    margin: 0;      
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  
  
  select.Filter:focus {
    background-image:
      linear-gradient(45deg, #ac0e28 50%, transparent 50%),
      linear-gradient(135deg, transparent 50%, #ac0e28 50%),
      linear-gradient(to right, #ccc, #ccc);
    background-position:
      calc(100% - 15px) 1em,
      calc(100% - 20px) 1em,
      calc(100% - 2.5em) 0.5em;
    background-size:
      5px 5px,
      5px 5px,
      1px 1.5em;
    background-repeat: no-repeat;
    border-color: #ac0e28;
    outline: 0;
  }
  
  
 